<template>
  <el-container>
    <el-aside width="240px" style="background-color: rgb(238, 241, 246);padding:5px;">
      <!--  -->
      <pm_search
        ref="pm_search"
        :hasPackNoSearch="false"
        :packRowHeight="10"
        :fields="searchFields"
        :entity="config.entity"
        v-on:getAllSearchParams="queryData"
      ></pm_search>
    </el-aside>
    <el-container>
      <el-main style="padding: 0 10px;overflow:hidden;">
        <pm_table ref="pmTable" tableKey="SelectStock-pmTable" :reload="afterSetting" 
                  v-if="reload" :dataSource="dataSource" :totalData="totalData" :config="config" :bottomHeight="100">
          <!-- <vue_column :prop="props.field" slot-scope="props" label="" allowEdit='true' width="200" placeholder=""
          :callback="requestTransactionLogs" renderType="input"></vue_column>-->
          <pm_column prop="packNo" label="" width="150"></pm_column>
          <pm_column prop="usableNum" label="" width="100"></pm_column>
          <pm_column prop="usableWeight" label="" width="100"></pm_column>
          <pm_column prop="cstId" label="" width="200"></pm_column>
          <!-- <pm_column prop="costDays" label="" width="100"></pm_column> -->
          <pm_column prop="brandId" label="()" width="150"></pm_column>
          <pm_column prop="textureId" label="()" width="110"></pm_column>
          <pm_column prop="spec" label="" width="120"></pm_column>
          <pm_column prop="producingId" label="" width="150"></pm_column>
          <pm_column prop="calcType" label="" width="100"></pm_column>
          <pm_column prop="measurementUnit" label="" width="100"></pm_column>
          <pm_column prop="number" label="" width="100"></pm_column>
          <pm_column prop="netWeight" label="" width="100"></pm_column>
          <pm_column prop="usedNum" label="" width="100"></pm_column>
          <pm_column prop="usedWeight" label="" width="100"></pm_column>
          <pm_column prop="realOutWeight" label="" width="100"></pm_column>
          <pm_column prop="grossWeight" label="" width="100"></pm_column>
          <pm_column prop="poundWeight" label="" width="100"></pm_column>
          <pm_column prop="unitNumber" label="" width="100"></pm_column>
          <pm_column prop="whsCalcStackId" label="" width="150"></pm_column>
          <pm_column prop="whsStackName" label="" width="70"></pm_column>
          <pm_column prop="whsLayerId" label="" width="70"></pm_column>
          <pm_column prop="status" label="" width="80"></pm_column>
          <pm_column prop="remark" label="" width="200"></pm_column>
          <pm_column prop="manufactureTime" label="" width="140"></pm_column>
          <pm_column prop="stoveCode" label="" width="100"></pm_column>
          <pm_column prop="whsinTime" label="" width="140"></pm_column>
          <pm_column prop="costTime" label="" width="150"></pm_column>
          <pm_column prop="transferTimes" label="" width="100"></pm_column>
        </pm_table>
        <pm_pagination ref="pager" :totalSize="totalSize" :pageSize="25" :queryData="getList"></pm_pagination>
      </el-main>
      <el-footer style="padding:0px;">
        <!-- toolbar -->
        <pm_tool_bar :noBackground="true">
          <pm_toolButton btnName="" btnIcon="el-icon-success" :btnClickFunc="ok"></pm_toolButton>
          <pm_toolButton btnName="" btnIcon="el-icon-close" :btnClickFunc="closeWin"></pm_toolButton>
        </pm_tool_bar>
      </el-footer>
    </el-container>
  </el-container>
</template>
<script>
import httpUtil from "@/common/utils/HttpUtil";
import metadata from "@/common/entities/Metadata";
import layerUtil from "@/common/utils/LayerUtil";
import commonUtil from "@/common/utils/CommonUtils";
import Table from "@/components/common/table/Table";
import pm_form_render from "@/components/common/form/pm_form_render";
import pm_form_item from "@/components/common/form/pm_form_item";
import pm_search from "@/components/vitems/pageSearch/pm_search";
import pm_table from "@/components/common/table/pm_table";
import {tableReload} from '@/components/common/table/table-reload';
import pm_column from "@/components/common/table/pm_column";
import pm_pagination from "@/components/common/table/pm_pagination";
import pm_toolButton from "@/components/common/button/pm_toolButton";
import pm_tool_bar from "@/components/common/table/pm_tool_bar";
export default {
  components: {
    x_table: Table,
    pm_search,
    pm_form_render,
    pm_form_item,
    pm_table,
    pm_column,
    pm_pagination,
    pm_toolButton,
    pm_tool_bar
  },
  mixins:[tableReload],
  data: function() {
    return {
      dataSource: [],
      totalData: null,
      config: {
        entity: this.$metaData.wmsStock,
        height: 450,
        allowEdit: false,
        multiple: true
      },
      totalSize: 0,
      formModel: { lockNo: "", status: "", cstId: "" },
      rules: {
        text1: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ]
      },
      // 
      searchFields: [
        {
          displayName: "",
          fieldName: "cardNo",
          xtype: "text",
          searchLoc: "1-1-24-9-0"
        },
        {
          displayName: "()",
          fieldName: "brandId",
          xtype: "select",
          searchLoc: "2-1-24-9-0"
        },
        {
          displayName: "()",
          fieldName: "textureId",
          xtype: "select",
          searchLoc: "3-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "spec",
          xtype: "text",
          searchLoc: "4-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "producingId",
          xtype: "select",
          searchLoc: "5-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "cstId",
          xtype: "select",
          searchLoc: "6-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "whsCalcStackId",
          xtype: "tree_select",
          searchLoc: "7-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "stackName",
          xtype: "text",
          searchLoc: "8-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "netWeight",
          xtype: "number",
          precision: 3,
          searchLoc: "9-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "grossWeight",
          xtype: "number",
          precision: 3,
          searchLoc: "10-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "poundWeight",
          xtype: "number",
          precision: 3,
          searchLoc: "11-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "whsInTime",
          dateBegin: "inTimeBegin",
          dateEnd: "inTimeEnd",
          xtype: "datetimerange",
          timeDefault: [],
          searchLoc: "12-1-24-9-0"
        },
        {
          displayName: "",
          fieldName: "packNo",
          xtype: "textarea",
          searchLoc: "13-1-24-9-0",
          rows:4,
        },
        {
          displayName: "",
          fieldName: "btn_search",
          xtype: "btn",
          searchLoc: "14-1-20-9-4"
        }
      ]
    };
  },
  created() {
    var fields = JSON.parse(this.$options.propsData.lockFields);
    fields.forEach(f => {
      var item = this.searchFields.find(a => a.fieldName == f);
      if (item) {
        this.$set(item, "disabled", true);
      }
    });
    //formModel
    this.formModel = JSON.parse(this.$options.propsData.filter);
    this.formModel.selectedValid = this.$options.propsData.selectedValid;
    //
    var $this = this;
    this.searchFields.forEach(a => {
      var param = this.formModel[a.fieldName];
      if (param != null) {
        $this.$set(a, "value", param);
      }
    });
  },
  methods: {
    /**
     * 
     */
    getList: function(page, size) {
      var $this = this;
      this.getData(data =>{
        $this.dataSource = Object.freeze(data.content);
        $this.totalSize = data.total;
      },page,size);
    },
    queryData: function(param) {
      var $this = this;
      //1.
      this.getData(data => {
        $this.totalData = data.content;
        //2.
        $this.$refs.pager.refreshData();
      });
    },
    /**
     * 
     */
    getData(callback,page,size) {
      var param = this.$refs.pm_search.getParam();
      param.page = page || 0;
      param.size = size || 100000;
      if (param.packNo != null) {
        param.fPackNo = param.packNo.split(/[\n]/);
      }
      this.$commonUtil.handler.packNoHander(param);
      //
      param = { ...this.formModel, ...param };
      var $this = this;
      this.$httpUtil.post("stock/getList", param, data => {
        //
        if (data.content) {
          var calc = $this.$metaData.wmsStock.methods.calcUsableProp;
          data.content.forEach(a => {
            calc(a);
          });
        }
        if (callback) {
          callback(data);
        }
      });
    },
    closeWin: layerUtil.closeWin,
    ok: function() {
      //
      var rows = this.$refs.pmTable.getSelectedRows();
      var returnList = [],
        errorMsgList = [],
        errorList = [];
      var hasError = false;
      var $this = this;
      rows.forEach(stock => {
        //
        if ($this.formModel.selectedValid) {
          //TODO:
          if (!$this.formModel.selectedValid(stock)) {
            hasError = true;
            errorList.push(stock);
            if ($this.formModel.errorMsg) {
              errorMsgList.push(
                "[" + stock.packNo + "]" + $this.formModel.errorMsg + "!"
              );
            } else {
              errorMsgList.push(
                "[" + stock.packNo + "],!"
              );
            }
            return;
          }
        } else {
          //TODO:(1:)
          if (stock.status != 1) {
            hasError = true;
            errorList.push(stock);
            errorMsgList.push(
              "[" + stock.packNo + "]" + stock.status_display + "!"
            );
            return;
          }
          if (stock.whsoutType == 1 && stock.usableNum <= 0) {
            hasError = true;
            errorList.push(stock);
            errorMsgList.push(
              "[" + stock.packNo + "]0!"
            );
            return;
          }
        }
        returnList.push(stock);
      });
      //TODO:
      if (hasError) {
        //
        this.$refs.pmTable.toggleRowSelection(errorList, false);
        this.$commonUtil.valid.throwEx(errorMsgList.join("<br/>"));
      }
      //
      this.$options.propsData.selected(returnList);
      //
      this.closeWin();
    }
  }
};
</script>


