import cacheUtil from './CacheUtil'
import httpUtil from './HttpUtil'
import {loadCache} from '../../api/api'

let isRunning = false;
let websock = null;
let wsUrl = httpUtil.serviceUrl.replace("http:","ws:").replace("https:","ws:");
if(!wsUrl.endsWith("/")){
    wsUrl+="/";
}
wsUrl +=  "socket";
/**
 * socket
 */
function initWebSocket() { 
    //1.
    var dic = cacheUtil.getDic();
    if(dic == null){
        //1.
        isRunning = false;
        try{
          websocketClose();
        }catch(ex){
          console.log(ex);
        }
    }
    if(isRunning) return;
    if(dic == null){
        //1.
        loadCache();
    }else{
        //
        setTimeout(function(){
            loadCache();
            console.log("");
        },0);
    }
    console.log("socket");
    //2.weosocket 
    var user = cacheUtil.getUser();
    if(user == null) return;
    const wsuri = wsUrl + "?token="+ user.token; //ws
    websock = new WebSocket(wsuri);
    websock.onopen = websocketOnOpen;
    websock.onerror = websocketOnError;
    websock.onmessage = websocketOnMessage;
    websock.onclose = websocketClose;
    //10
    
    setInterval(function(){
        websocketSend("");
    },10000);
};

function websocketOnOpen() {
    isRunning = true;
    console.log("WebSocket");
}

function websocketOnError(e) { //
    isRunning = false;
    console.log("WebSocket");
};
function websocketClose(e) { // 
    isRunning = false;
    console.log("connection closed (" + e.code + ")");
}
function websocketOnMessage(e) { // 
    const redata = JSON.parse(e.data);
    console.log("websocket:");
    console.log(redata);
    //
    refreshCache(redata);
}
/**
 * 
 * @param {*} agentData 
 */
function websocketSend(agentData) { // 
    if(websock==null) return;
    websock.send(agentData);
};

//
function refreshCache(msg){
    try{
        if(msg!=null && msg.content!=null && msg.title.includes("cache_change")){
            var isAll = (msg.content == "-1000");
            var user = cacheUtil.getUser();
            var isMatchAccount = user != null && msg.content == user.accountId;
            //
            if (!isAll && !isMatchAccount) return;
            //
            loadCache(null,dic=>{
               //
               cacheUtil.notify(dic);
            });
        }
    }catch(e){
        console.log(e);
    }
}

export default {
    created: function () {
        //
        initWebSocket();
    },
    destroyed: function () {
        //
        websocketClose();
    },
    send:function(data){
        //
        websocketSend(data);
    }
}


